.place{
  position: absolute; 
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  left: -10px;
  top: -10px;  
  border: 1px solid blue;
  background: red;
  color: yellow; 
}
:root {
  --blue: #0e0620;
  --white: #fff;
  --green: #2ccf6d;
}

button {
  font-family: "Nunito Sans";
}

h1 {
  font-size: 7.5em;
  margin: 15px 0px;
  font-weight: bold;
}

h2 {
  font-weight: bold;
}


.btnPNF {
  z-index: 1;
  overflow: hidden;
  background: transparent;
  position: relative;
  padding: 8px 50px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 1em;
  letter-spacing: 2px;
  transition: 0.2s ease;
  font-weight: bold;
  margin: 5px 0px;
}
.btnPNF.green {
  border: 4px solid #2ccf6d;
  border: 4px solid var(--green);
  color: #0e0620;
  color: var(--blue);
}
.btnPNF.green:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0%;
  height: 100%;
  background: #2ccf6d;
  background: var(--green);
  z-index: -1;
  transition: 0.2s ease;
}
.btnPNF.green:hover {
  color: #fff;
  color: var(--white);
  background: #2ccf6d;
  background: var(--green);
  transition: 0.2s ease;
}
.btnPNF.green:hover:before {
  width: 100%;
}

